import m from "mithril";

export default class Deck {
    view(vnode) {
        const attrs = vnode.attrs;

        const cardObjects = attrs.cardObjects;
        return (
            <div class="content">
                <div id="grid">

                    {Array.from(cardObjects).map(
                        (keyValue, i) => {
                            const card = keyValue[1]
                            const id = keyValue[0]

                            return <div>
                                <Card card = {card}></Card>
                            </div>
                        }
                    )
                    }

                </div>
            </div>
        )
    }
}

class Card {
    view(vnode) {
        const attrs = vnode.attrs;

        const card = attrs.card;
        const id = attrs.id;

        const image = card.image_uris !== undefined ? card.image_uris.normal : ''; //TODO: handle undefined
        return (
            <>
                <img src={image} style='height: 250px;'></img>
                <div>
                    <button>-</button>
                    {4}
                    <button>+</button>
                </div>
            </>
        )
    }
}