import m from "mithril";
import SearchForm from "./features/search/components/searchForm.js"
import SearchList from "./features/search/components/searchList.js"
import MtgMock from "./api/mtgMock.js";
import Search from "./features/search/components/search.js";
import Deck from "./features/deck/components/deck.js";
import Stats from "./features/deck/components/stats.js";

class App {
    view(vnode) {
        const mock = new MtgMock();

        const cards = [mock.loadCards({})[0], 
        mock.loadCards({})[1], 
        mock.loadCards({})[2],
        mock.loadCards({})[3], 
        mock.loadCards({})[4],
        mock.loadCards({})[5], 
        mock.loadCards({})[6],
        mock.loadCards({})[7], 
        mock.loadCards({})[8],
        mock.loadCards({})[9], 
        mock.loadCards({})[10],
        mock.loadCards({})[11], 
        mock.loadCards({})[12]
    ];

        const cardObjects = new Map([
            [cards[0].id, cards[0]],
            [cards[1].id, cards[1]],
            [cards[2].id, cards[2]],
            [cards[3].id, cards[3]],
            [cards[4].id, cards[4]],
            [cards[5].id, cards[5]],
            [cards[6].id, cards[6]],
            [cards[7].id, cards[7]],
            [cards[8].id, cards[8]],
            [cards[9].id, cards[9]],
            [cards[10].id, cards[10]],
            [cards[11].id, cards[11]],
            [cards[12].id, cards[12]],
        ]);

        const attrs = vnode.attrs;
        return (
            <>
                <header>
                    MTG Deck Builder
                </header>
                <main class = 'main'>
                    <Search showBasics={false}></Search>
                    <Deck cardObjects = {cardObjects}>
                    </Deck>
                </main>
                <Stats></Stats>
            </>
        )
    }
}

m.mount(document.body, {
    view: function () {
        return m(App);
    }
})

