import m from "mithril";
import ManaCostStatsWidget from "../../../widgets/manaCostStatsWidget";
import ColorStatsWidget from "../../../widgets/colorStatsWidget";

export default class Stats {
    view(vnode) {
        return (<div id="stats">
            <h2>Stats</h2>
            <div class="widgets">
                <ManaCostStats data={[
                    { cost: 0, count: 2 },
                    { cost: 1, count: 8 },
                    { cost: 2, count: 12 },
                    { cost: 3, count: 15 },
                    { cost: 4, count: 10 },
                    { cost: 5, count: 6 },
                    { cost: 6, count: 4 },
                    { cost: '7+', count: 3 }
                ]}>
                </ManaCostStats>
                <ColorStats data={[
                    { color: 'White', count: 15 },
                    { color: 'Blue', count: 12 },
                    { color: 'Black', count: 8 },
                    { color: 'Red', count: 10 },
                    { color: 'Green', count: 18 },
                    { color: 'Colorless', count: 7 }
                ]}>
                </ColorStats>
            </div>

        </div>)
    }
}

class ManaCostStats {
    oncreate(vnode) {
        ManaCostStatsWidget.buildStats(vnode.dom, vnode.attrs.data);
    }
    onupdate(vnode) {
        vnode.dom.innerHTML = "";
        ManaCostStatsWidget.buildStats(vnode.dom, vnode.attrs.data);
    }
    view(vnode) {
        return (<div class='widgets'>
            
        </div>)
    }
}

class ColorStats {
    oncreate(vnode) {
        ColorStatsWidget.buildStats(vnode.dom, vnode.attrs.data);
    }
    onupdate(vnode) {
        vnode.dom.innerHTML = "";
        ColorStatsWidget.buildStats(vnode.dom, vnode.attrs.data);
    }
    view(vnode) {
        return (<div class='widgets'>

        </div>)
    }
}