import m from "mithril";
import MtgMock from "~/src/api/mtgMock.js";
import SearchForm from "./searchForm.js";
import SearchList from "./searchList.js";

export default class Search {
    view(vnode) {
        const attrs = vnode.attrs;

        const showBasics = attrs.showBasics;
        return (
            <div id="search">
                {attrs.showBasics ? 
                <SearchBasic></SearchBasic> : 
                <SearchNonBasic></SearchNonBasic>}
            </div>
        )
    }
}
class SearchBasic {
    view(vnode) {
        mock = new MtgMock();
        return (
            <>
                <button>Switch to non-basics</button>
                <SearchList
                    cards={mock.loadBasicCards()}
                    onAdd={(card) => console.log(card)}>
                </SearchList>
            </>
        )
    }
}

class SearchNonBasic {
    view(vnode) {
        const mock = new MtgMock();
        const attrs = vnode.attrs;
        return (
            <>
                <button>Switch to basics</button>
                <SearchForm></SearchForm>
                <SearchList
                    cards={mock.loadCards({})}
                    onAdd={(card) => console.log(card)}>
                </SearchList>
            </>
        )
    }
}